const Animation = () => {
	const sectionsAnim = document.querySelectorAll('[data-animation]');
	const sectionsAnimStart = document.querySelectorAll('[data-animation-start]');
	let lastScroll = 0;

	window.addEventListener('scroll', () => {
		let currentScroll = document.documentElement.scrollTop || document.body.scrollTop;
		if (!document.querySelectorAll('[data-animation]:not(.visible)')) return;
		for (const section of sectionsAnim) {
			if ( section.getBoundingClientRect().top <= window.innerHeight * 0.75) {
				section.classList.add('visible');
			} else {
				section.classList.remove('visible');
			}
		}
	});

	sectionsAnimStart.forEach(item => {
		item.classList.add('visible');
	});
};

export default Animation;
