import { gsap }         from 'gsap'; // Import the gsap library and ScrollToPlugin
import ScrollTrigger from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

const NavFixed = () => {
	let lastScroll = 0;
	const sections = document.querySelectorAll('.js-scroll-section');

	window.addEventListener('scroll', () => {
		let currentScroll = document.documentElement.scrollTop || document.body.scrollTop;
		let scrollY = window.pageYOffset;

		if (currentScroll > 0 && lastScroll <= currentScroll) {
			lastScroll = currentScroll;
			sections.forEach(current => {
				const sectionHeight = current.offsetHeight;
				const sectionTop = current.getBoundingClientRect().top + window.pageYOffset - 400;
				let sectionId = current.getAttribute('id');
				let sectionName = document.getElementById(sectionId);

				if (sectionTop <= scrollY && sectionTop + sectionHeight > scrollY) {
					sectionName.classList.add('is-active');
				}
			});
		} else {
			lastScroll = currentScroll;
			sections.forEach(current => {
				const sectionHeight = current.offsetHeight;
				const sectionTop =
					current.getBoundingClientRect().top + window.pageYOffset;
				let sectionId = current.getAttribute('id');
				let sectionName = document.getElementById(sectionId);

				if (sectionTop >= scrollY) {
					if (sectionName) {
						sectionName.classList.remove('is-active');
					}
				}
			});
		}
	});

	if (window.matchMedia('(min-width: 992.2px)').matches) {
		const timeItems = gsap.utils.toArray('.article__time')

		timeItems.forEach((item) => { 
			gsap.to(item, {
				scrollTrigger : {
					trigger : '.js-bottom-nav',
					start : 'top 100%',
					end : 'bottom 0%',
					scrub : true,
				},
				y: -890,
			});
		});
	}
};

export default NavFixed;
